@charset "UTF-8";
/* ¯¯¯¯¯¯¯¯¯ *\
  Download
\* ˍˍˍˍˍˍˍˍˍ */
.fr-download {
  --text-spacing: 0;
  --title-spacing: 0;
  position: relative;
  padding-bottom: 1.5rem;
  margin: 0 0 1rem 0;
}

.fr-download__link {
  padding: 0;
}

.fr-download__link::after {
  content: "";
  flex: 0 0 auto;
  display: inline-block;
  vertical-align: middle;
  background-color: currentColor;
  width: var(--icon-size);
  height: var(--icon-size);
  -webkit-mask-size: 100% 100%;
  mask-size: 100% 100%;
}

.fr-download__link::after {
  -webkit-mask-image: url("../../icons/system/download-line.svg");
  mask-image: url("../../icons/system/download-line.svg");
}

.fr-download__link::after {
  --icon-size: 1rem;
}

.fr-download__link::after {
  margin-left: 0.25rem;
  margin-bottom: 0.25rem;
}

.fr-download__desc {
  font-size:  0.875rem ;
  line-height:  1.5rem ;
  font-weight: 400;
  display: none;
}

.fr-download__detail {
  position: absolute;
  bottom: 0;
  left: 0;
  font-size:  0.75rem ;
  line-height:  1.25rem ;
  font-weight: 400;
  max-width: 100%;
  white-space: nowrap;
  pointer-events: none;
  cursor: text;
  overflow: hidden;
  text-overflow: ellipsis;
}

.fr-download__detail abbr {
  text-decoration: none;
}

.fr-download--card {
  padding: 1.5rem 1.5rem 4rem 1.5rem;
  --text-spacing: 0;
  --title-spacing: 0;
}

@media (min-width: 48em) {
  .fr-download--card {
    padding: 2rem 2rem 5rem 2rem;
  }
}

.fr-download--card .fr-download__link {
  font-weight:  700 ;
  font-size:  1.25rem ;
  line-height:  1.75rem ;
}

@media (min-width: 48em) {
  .fr-download--card .fr-download__link {
    font-size:  1.375rem ;
    line-height:  1.75rem ;
  }
}

.fr-download--card .fr-download__link::after {
  position: absolute;
  right: 1.5rem;
  bottom: 1.5rem;
  --icon-size: 1rem;
  margin: 0;
}

@media (min-width: 48em) {
  .fr-download--card .fr-download__link::after {
    --icon-size: 1.5rem;
    bottom: 2rem;
    right: 2rem;
  }
}

.fr-download--card .fr-download__desc {
  display: block;
  margin-top: 0.5rem;
}

@media (min-width: 48em) {
  .fr-download--card .fr-download__desc {
    margin-top: 0.75rem;
  }
}

.fr-download--card .fr-download__detail {
  position: absolute;
  bottom: 1.5rem;
  left: 1.5rem;
  max-width: calc(100% - 6.25rem);
}

@media (min-width: 48em) {
  .fr-download--card .fr-download__detail {
    bottom: 2rem;
    left: 2rem;
  }
}

.fr-download--card .fr-download__detail[class^="fr-icon-"]::before, .fr-download--card .fr-download__detail[class*=" fr-icon-"]::before, .fr-download--card .fr-download__detail[class^="fr-fi-"]::before, .fr-download--card .fr-download__detail[class*=" fr-fi-"]::before {
  --icon-size: 1rem;
  margin-right: 0.5rem;
}

.fr-downloads-group__title {
  font-weight:  700 ;
  font-size:  1.375rem ;
  line-height:  1.75rem ;
  margin: var(--title-spacing);
}

@media (min-width: 48em) {
  .fr-downloads-group__title {
    font-size:  1.5rem ;
    line-height:  2rem ;
  }
}

.fr-downloads-group--bordered {
  padding: 1.5rem 1.5rem 0.5rem;
}

@media (min-width: 48em) {
  .fr-downloads-group--bordered {
    padding: 2rem 2rem 0.5rem;
  }
}

.fr-downloads-group > ul {
  margin-top: 0;
  margin-bottom: 0;
}

.fr-downloads-group > ul > li {
  padding-bottom: 0;
}

.fr-downloads-group .fr-download {
  margin-bottom: 1rem;
}

@media (min-width: 48em) {
  .fr-downloads-group .fr-download {
    margin-bottom: 1.5rem;
  }
}

.fr-download--card {
  background-color: var(--background-default-grey);
  --hover: var(--background-default-grey-hover);
  --active: var(--background-default-grey-active);
  box-shadow: inset 0 0 0 1px var(--border-default-grey);
}

.fr-download--card .fr-download__link {
  color: var(--text-title-grey);
}

.fr-download--card .fr-download__link::after {
  color: var(--text-action-high-blue-france);
}

.fr-download__link {
  color: var(--text-action-high-blue-france);
}

.fr-download__detail {
  color: var(--text-mention-grey);
}

.fr-downloads-group--bordered {
  box-shadow: inset 0 0 0 1px var(--border-default-grey);
}
